.header {
  position: fixed;
  background-color: #0f0f0f;
  z-index: 999;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 75px;
  .header-content {
    display: flex;
    align-items: center;
    max-width: 1140px;
    margin: auto;
    .left {
      padding-left: 16px;
      img {
        width: 50px;
      }
    }
    .center {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      .search-box {
        display: flex;
        flex-direction: row;
        border: 1px solid rgba(255, 255, 255, 0.2);
        width: 80%;
        max-width: 350px;
        height: 45px;
        border-radius: 4px;
        input {
          width: 80%;
          background-color: #0f0f0f;
          border: none;
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
          color: #fff;
          padding: 0 10px;
        }
        button {
          width: 20%;
          background-color: rgba(255, 255, 255, 0.2);
          color: #fff;
          border: none;
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
          cursor: pointer;
          font-size: 24px;
        }
      }
    }
    .right {
      padding-right: 16px;
      img {
        width: 60px;
        border-radius: 50%;
        cursor: pointer;
      }
    }
  }
}
